/**
 *  Status badge
 */
.status {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 19px;
  color: #f5f5f5;
}
.status::before {
  content: "";
